






















import common from '../../common/common.js'
export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad() {
		this.loadData();
	},
	methods: {
		async loadData() {
			let that = this;
			let list = await this.$api.json('cateList');
			// list.forEach(item => {
			// 	if (!item.pid) {
			// 		that.flist.push(item); //pid为父级id, 没有pid或者pid=0是一级分类
			// 	} else if (!item.picture) {
			// 		that.slist.push(item); //没有图的是2级分类
			// 	} else {
			// 		that.tlist.push(item); //3级分类
			// 	}
			// })

			uni.request({
				url: common.category_listUrl, //仅为示例，并非真实接口地址。
				data: {
					is_mobile: 1,
					user_id: 0
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(res.data.status)
					if (res.data.status == 1) {
						that.flist = [];
						that.slist = [];
						that.tlist = [];
						list = res.data.data;
						list.forEach(item => {
							if (item.pid == 0) {
								that.flist.push(item); //pid为父级id, 没有pid或者pid=0是一级分类
								that.slist.push(item); //没有图的是2级分类
							} else if (item.class_layer==1) {
								
							} else {
								that.tlist.push(item); //3级分类
							}
						})
						that.currentId = res.data.currentId;



					} else {



					}



				}
			});

		},
		//一级分类点击
		tabtap(item) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}

			this.currentId = item.id;
			let index = this.slist.findIndex(sitem => sitem.fid === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				this.currentId = tabs[0].fid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(sid, tid, title) {
			uni.navigateTo({
				url: `/pages/product/list?fid=${this.currentId}&sid=${sid}&tid=${tid}&title=${title}`
			})
		}
	}
}
